.color-picker-wrapper .color-picker-swatch.color-picker-swatch-left {
  right: 18px; }

.color-picker-wrapper .color-picker-swatch {
  top: 3px;
  width: 28px;
  height: 28px;
  border-radius: 3px;
  position: absolute;
  vertical-align: middle;
  background-position: -80px 0;
  border: 1px solid #ccc;
  padding: 0;
  margin: 0;
  display: inline-block;
  cursor: pointer; }

.color-picker-wrapper .color-picker-swatch .color-picker-swatch-color {
