  -webkit-box-shadow: -4px 0px 7px -1px rgba(0, 0, 0, 0.23);
  -moz-box-shadow: -4px 0px 7px -1px rgba(0, 0, 0, 0.23);
  box-shadow: -4px 0px 7px -1px rgba(0, 0, 0, 0.23); }
  .server-error .page-header {
    background-color: #e1ae19;
    color: white;
    font-weight: bold;
    height: 65px;
    text-align: center;
    vertical-align: middle;
    border: none; }
    .server-error .page-header h1 {
